@import "../../style/App.scss";

.features {
  background-color: $black-color;
  padding: 60px 0;

  .wrapper {
    display: flex;
    flex-direction: column;
  }

  &__header {
    text-align: left;
    margin-bottom: 40px;

    .big__title {
      color: white;
    }

    p {
      color: white;
    }
  }

  &__title {
    font-size: 2.4rem;
    color: $black-color;
    margin-bottom: 10px;
  }

  &__subtitle {
    font-size: 1.6rem;
    color: #fff;
    max-width: 600px;
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }

  .feature__item {
    background-color: #f8f8f8;
    border-radius: 8px;
    padding: 30px;
    transition: all 0.3s ease;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);

    &:hover {
      transform: translateY(-5px);
      box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
    }
  }

  .feature__icon {
    background-color: $main-color;
    color: #ffffff;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
  }

  .feature__title {
    font-size: 1.8rem;
    margin-bottom: 15px;
    color: $black-color;
  }

  .feature__description {
    font-size: 1.4rem;
    color: #666;
  }
}

@media screen and (max-width: 768px) {
  .features {
    &__header {
      text-align: center;
      align-items: center;
    }

    &__subtitle {
      margin: 0 auto;
    }

    &__grid {
      grid-template-columns: 1fr;
    }
  }
}
