@import "../../style/App.scss";

.services {
  background-color: $black-color;
  padding: 60px 0;

  .wrapper {
    display: flex;
    flex-direction: column;
  }

  &__header {
    text-align: left;
    margin-bottom: 40px;
    .big__title {
      color: white;
    }

    p {
      color: white;
    }
  }

  &__title {
    font-size: 2.4rem;
    color: $black-color;
    margin-bottom: 10px;
  }

  &__subtitle {
    font-size: 1.6rem;
    color: #666;
    max-width: 600px;
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 20px;
  }

  .service__item {
    background-color: #f8f8f8;
    border-radius: 8px;
    padding: 30px;
    transition: all 0.3s ease;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    &:hover {
      background-color: $main-color;
      color: #ffffff;

      .service__icon {
        background-color: #ffffff;
        color: $main-color;
      }

      .service__link {
        color: #ffffff;
      }
    }
  }

  .service__icon {
    background-color: $main-color;
    color: #ffffff;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    transition: all 0.3s ease;
  }

  .service__title {
    font-size: 1.8rem;
    margin-bottom: 15px;
  }

  .service__description {
    font-size: 1.4rem;
    margin-bottom: 20px;
  }

  .service__link {
    color: $main-color;
    font-size: 1.4rem;
    text-decoration: none;
    font-weight: 600;
    transition: all 0.3s ease;

    &:hover {
      text-decoration: underline;
    }
  }
}

@media screen and (max-width: 768px) {
  .services {
    &__header {
      text-align: center;
      align-items: center;
    }

    &__subtitle {
      margin: 0 auto;
    }

    &__grid {
      grid-template-columns: 1fr;
    }
  }
}
