@import "../../../style/App.scss";

.contact__social {
  width: 100%;
  margin: 60px 0 10px 0;
  transition: filter 0.2s ease; // Ensure smooth transition
  cursor: pointer;
}

.contact__social:hover {
  filter: invert(52%) sepia(99%) saturate(292%) hue-rotate(228deg)
    brightness(101%) contrast(106%);
}

@media screen and (max-width: 990px) {
  .contact__social {
    text-align: center;
    width: 100%;
    img {
      margin: 0 auto;
    }
  }
}
