@import "../../style/App.scss";

#about {
  padding: 60px 0;
  .custom-shape-divider-bottom-1728038572 {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
    transform: rotate(180deg);
  }

  .custom-shape-divider-bottom-1728038572 svg {
    position: relative;
    display: block;
    width: calc(100% + 1.3px);
    height: 57px;
  }

  .custom-shape-divider-bottom-1728038572 .shape-fill {
    fill: #ffffff;
  }
}

@media screen and (max-width: 990px) {
  #about {
    text-align: center;
  }
}
