@import "../../style/App.scss";

.hero {
  padding-top: 240px;
  width: 100%;
  padding-bottom: 60px;
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  .hero-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.5));
    z-index: 1;
  }

  .wrapper {
    position: relative;
    z-index: 2;
  }

  .hero-info {
    padding-top: 60px;

    h1,
    p {
      color: $white-color;
    }

    p {
      padding-top: 20px;
      padding-bottom: 50px;
    }
  }

  .hero-image {
    max-width: 570px;
    height: auto;
    float: right;

    img {
      max-width: 100%;
      height: auto;
    }
  }
}

@media screen and (max-width: 1080px) {
  .hero {
    padding-top: 140px;

    .hero-info {
      padding-top: 0px;
    }

    .hero-image {
      float: none;
      margin: 0 auto;
      margin-top: 50px;
    }
  }
}

@media screen and (max-width: 990px) {
  .hero {
    .hero-info {
      text-align: center;
    }
  }
}
